import uniGrid from '@dcloudio/uni-ui/lib/uni-grid/uni-grid.vue';
import uniGridItem from '@dcloudio/uni-ui/lib/uni-grid-item/uni-grid-item.vue';
import redirect from '../../mixins/redirect.js';
let startY = 0,
	moveY = 0,
	pageAtTop = true;
export default {
	name: 'UserIndex',
	mixins: [redirect],
	components: {
		uniGrid,
		uniGridItem
	},
	data() {
		return {
			coverTransform: 'translateY(0px)',
			coverTransition: '0s',
			moving: false,
			userInfo: {},
			showView: false
		};
	},
	mounted() {},
	created() {
		// this.getUserSetting()
	},
	onLoad() {},
	onShow() {
		this.getUserInfo();
		uni.showTabBar();
	},
	methods: {
		toOrder() {
			this.needTokenQueryLink('/pages/user/order/lists');
		},
		toFavorite() {
			this.needTokenQueryLink('/pages/user/favorite');
		},
		toVip() {
			wx.navigateToMiniProgram({
				appId: 'wxe6fdabad206fe97b',
				success: result => {
					console.log(result);
				},
				fail: err => {
					console.log(err);
				},
				complete: () => {}
			});
		},
		toSetting() {
			uni.navigateTo({
				url: `setting/setting?info=${encodeURIComponent(JSON.stringify(this.userInfo))}`
			});
		},
		getUserInfo() {
			this.$http({
				aurl: 'userInfo',
				success: res => {
					this.userInfo = res.data.result;
				},
				fail: err => {
					console.log(err);
				}
			});
		},
		/**
		 *  会员卡下拉和回弹
		 *  1.关闭bounce避免ios端下拉冲突
		 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
		 *    transition设置0.1秒延迟，让css来过渡这段空窗期
		 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
		 */
		coverTouchstart(e) {
			if (pageAtTop === false) {
				return;
			}
			this.coverTransition = 'transform .1s linear';
			startY = e.touches[0].clientY;
		},
		coverTouchmove(e) {
			moveY = e.touches[0].clientY;
			let moveDistance = moveY - startY;
			if (moveDistance < 0) {
				this.moving = false;
				return;
			}
			this.moving = true;
			if (moveDistance >= 80 && moveDistance < 100) {
				moveDistance = 80;
			}

			if (moveDistance > 0 && moveDistance <= 80) {
				this.coverTransform = `translateY(${moveDistance}px)`;
			}
		},
		coverTouchend() {
			if (this.moving === false) {
				return;
			}
			this.moving = false;
			this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
			this.coverTransform = 'translateY(0px)';
		},
		callUp() {
			uni.makePhoneCall({
				phoneNumber: '13544410650'
			});
		}
	}
};
